import{LruMap}from"../lru.js";export const promiseCache=new LruMap(8192);export function withDedupe(fn,{enabled:enabled=!0,id:id}){if(!enabled||!id)return fn();if(promiseCache.get(id))return promiseCache.get(id);const promise=fn().finally(()=>promiseCache.delete(id));return promiseCache.set(id,promise),promise}