export function formatUnits(value,decimals){let display=value.toString();const negative=display.startsWith("-");negative&&(display=display.slice(1)),display=display.padStart(decimals,"0");let[integer,fraction]=[display.slice(0,display.length-decimals),display.slice(display.length-decimals)];return fraction=fraction.replace(/(0+)$/,""),`${negative?"-":""}${integer||"0"}${fraction?`.${fraction}`:""}`}